@import "~Styles/colors.module";
@import "~Styles/mixins.module";

$checkbox-color: $color-very-light-blue;

.rtl {
  flex-flow: row-reverse;

  .checkbox_text {
    margin-right: 7px;
  }
}

.input_checkbox {
  border-radius: 5px;
  border: solid 1px $checkbox-color;
}

.checkbox_text {
  font-size: 18px;
  color: $primary-text-color;
  margin-left: 7px;
}

@include medium {
  .checkbox_text {
    font-size: 16px;
  }
}

@include small {
  .checkbox_text {
    font-size: 14px;
  }
}
.image_container {
  padding: 17px 0px 17px 54px;
  flex-wrap: wrap;

  .image_contain {
    margin-left: 15px;
    margin-top: 15px;
  }
  .image {
    object-fit: contain;
    max-height: 150px;
  }
}
.inner_dropzone_container {
  padding: 40px;
}
